import React, { Component } from "react";
import styled from "styled-components";

export default class Channel extends Component {
  getTheme=()=> {
    const { status } = this.props;
    switch (status) {
      case "live":
        return {
            fontcolor: "white",
            fontbgcolor: "red",
            fontcontent: "LIVE",
            bordercolor: "red",
        };
      case "wait":
        return {
            fontcolor: "white",
            fontbgcolor: "#34afeb",
            fontcontent: "WAIT",
            bordercolor: "#6a97ad",
        };
      case "off":
        return {
            fontcolor: "#f9fae6",
            fontbgcolor: "#616161",
            fontcontent: "OFF",
            bordercolor: "#616161",
        };
      default:
        return {
            fontcolor: "#f0d8e2",
            fontbgcolor: "#cb4ede",
            fontcontent: "FAIL",
            bordercolor: "#6c3075",
        };
    }
  }
  render() {

    const theme = this.getTheme();

    const Status = styled.p`
      position: relative;
      top: 48px;
      left: 14px;
      width: 46px;
      height: 20px;
      text-align: center;
      font-weight: bold;
      font-size: 16px;
      color: ${theme.fontcolor};
      background: ${theme.fontbgcolor};
    `;

    const Container = styled.div`
      flex-basis: 18%;
      margin: 10px;
      border: solid 2px ${theme.bordercolor};
      border-radius: 5px;
    `;

    const Avatar = styled.img`
      position: absolute;
      width: 70px;
      height: 70px;
      border-radius: 50%;
      border: 3px solid ${theme.bordercolor};
      overflow: hidden;
    `;
    return (
      <Container>
        <Avatar src={this.props.avatar}></Avatar>
        <Status>{theme.fontcontent}</Status>
        <a href={this.props.streamurl}>
          <Image src={this.props.thumbnail} />
        </a>
        <Body>
          <Title>{this.props.title}</Title>
          <Owner>{this.props.owner}</Owner>
          <ViewCount>{this.props.viewcount}</ViewCount>
          <Description>{this.props.starttime}</Description>
        </Body>
      </Container>
    );
  }
}

const Title = styled.p`
  font-size: 12px;
  text-align: center;
`;

const Body = styled.div`
  padding: 10px;
`;

const Owner = styled.h1`
  font-size: 15px;
  text-align: center;
`;

const ViewCount = styled.h1`
  font-size: 15px;
  text-align: center;
`;

const Description = styled.p`
  font-size: 12px;
  text-align: center;
`;

const Image = styled.img`
  width: 100%;
`;
