import React, { Component } from "react";
import styled from "styled-components";

export default class Search extends Component {

  handleKeyUp = (e) => {
    console.log(e)
    const { keyCode, target } = e;

    // 測試你輸入的按鍵的keycode值
    // alert(keyCode)

    // 保存數據
    console.log(target.value);
    this.inputvalue = target.value;

    // 當按下enter時，提交輸入結果
    if (keyCode !== 13) {
      return; //不是enter就返回
    }

    // 提交數據
    this.props.onSubmit(this.inputvalue);
  };

  handleClick = (e) => {
    console.log(e)
    // 提交數據
    this.props.onSubmit(this.inputvalue);
  };

  render() {
    return (
      <div>
        <Input
          type="text"
          placeholder="Enter keywords to get some channels"
          onKeyUp={this.handleKeyUp}
        />
        <Button onClick={this.handleClick}>搜尋</Button>
      </div>
    );
  }
}

const Input = styled.input`
  width: 60%;
  padding: 10px 20px;
  border: solid 2px #686868;
  border-radius: 5px;
  margin-right: 5px;
  outline: none;
  &:focus {
    border-color: #005ad8;
  }
`;

const Button = styled.button`
  padding: 10px 20px;
  border: solid 2px #005ad8;
  border-radius: 5px;
  background-color: #005ad8;
  color: white;
  outline: none;
  cursor: pointer;
`;
